@mixin scrollbar {
  ::-webkit-scrollbar {
    width: 10px;
  }

  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  ::-webkit-scrollbar-thumb {
    background: var(--color-orange-light);
    border-radius: var(--border-radius-normal);
  }

  ::-webkit-scrollbar-thumb:hover {
    background: var(--color-orange);
  }
}
